<template>
	<div id="addJob">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">发布招聘职位</div>
        <div class="van-nav-bar__right" @click="check_input">
          发布
        </div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;">工作地点</span>
        <input class="font_14 login_input" style="padding: 5px;" type="text" placeholder="请输入工作地点" v-model="address"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;">工作年限</span>
        <select class="font_14 select-bar" v-model="age">
          <option>不限</option>
          <option>一年以上</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;">福利待遇</span>
        <select class="font_14 select-bar" v-model="daiyu">
          <option>面议</option>
          <option>五险一金</option>
          <option>话补</option>
          <option>饭补</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;">学历要求</span>
        <select class="font_14 select-bar" v-model="xueli">
          <option>不限</option>
          <option>高中</option>
          <option>中专</option>
          <option>大专</option>
          <option>本科以上</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom div-bar2">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;border-right: 1px solid #8b8b8b;">招聘职位</span>
        <input class="font_14 login_input" type="text" placeholder="请填写招聘职位" v-model="title"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;border-right: 1px solid #8b8b8b;">薪资</span>
        <select class="font_14 select-bar" v-model="price">
          <option>2000元左右</option>
          <option>3000-5000元</option>
          <option>5000-8000元</option>
          <option>10000元以上</option>
          <option>面议</option>
        </select>
        <span>月</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;border-right: 1px solid #8b8b8b;">要求描述</span>
        <input class="font_14 login_input" type="text" placeholder="请输入" v-model="content"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;border-right: 1px solid #8b8b8b;">联系电话</span>
        <input class="font_14 login_input" type="text" placeholder="请输入" v-model="tell"/>
      </div>
      <div class="login_bar background_color_ffffff div-bar3" style="margin: 0 5px">
        <span class="font_14 text_center" style="display: inline-block;width: 20%;padding-right: 10px;border-right: 1px solid #8b8b8b;">联系人</span>
        <input class="font_14 login_input" type="text" placeholder="请输入" v-model="uname"/>
      </div>
      <div class="login_bar background_color_ffffff login_top" style="margin-top: 40px">
        <img :src="item" class="img_service" v-for="(item,index) in imgList" @click="choice(index)"/>
        <img src="static/img/icon_tjtp_tjps.png" class="img_service" @click="uploadImg"/>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'addJob',
		data() {
			return {
        address:'',
        age:'不限',
        daiyu:'面议',
        xueli:'不限',
        uname:'',
        userId:localStorage.getItem('userId'),
        title:'',
        price:'2000元左右',
        content:'',
        tell:'',
        type: 5,
        imgList:[],
			}
		},
		methods:{
      choice(index){
        this.$dialog.confirm({
          message: '请选择切换或删除该张图片',
          confirmButtonText:'删除',
          cancelButtonText:'切换'
        }).then(() => {
          let newImgList = [];
          for(let i=0;i<this.imgList.length;i++){
            if(i!=index){
              newImgList.push(this.imgList[i]);
            }
          }
          this.imgList = newImgList;
        }).catch(() => {
          this.changeImg(index)
        });
      },
      changeImg(dataMap){
        let $this = this;
        dsBridge.call('uploadImg');
        dsBridge.register('addImgCallback', function(rpdata) {
          if(rpdata.split(',').length>1){
            $this.$toast({
              message: '请上传一张图片',
              position: "bottom"
            });
          }else{
            let imgList1 = [];
            for(let i =0;i<$this.imgList.length;i++){
              if(i==dataMap){
                imgList1.push(rpdata)
              }else{
                imgList1.push($this.imgList[i])
              }
            }
            $this.imgList = imgList1;
          }
        })
      },
      uploadImg(){
        let $this = this;
        if(this.imgList.length==9){
          $this.$toast({
            message: '最多可以上传9张图片!',
            position: "bottom"
          });
        }else if(this.imgList.length<9){
          dsBridge.call('uploadImg');
          dsBridge.register('addImgCallback', function(rpdata) {
            if((rpdata.split(',').length+$this.imgList.length)>9){
              $this.$toast({
                message: '最多可以上传9张图片!',
                position: "bottom"
              });
            }else{
              for(let i=0;i<rpdata.split(',').length;i++){
                $this.imgList.push(rpdata.split(',')[i]);
              }
            }
          })
        }
      },
      check_input(){
        if(this.imgList.length<1){
          this.$toast({
            message: '请上传至少一张照片!',
            position: "bottom"
          });
          return;
        }
        if(!this.title||!this.content
          ||!this.address||!this.uname||!this.tell||!this.tell){
          this.$toast({
            message: '请将内容补充完整!',
            position: "bottom"
          });
        }else{
          this.addJob();
        }
      },
      addJob(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          title: $this.title,
          content: $this.content,
          price: $this.price,
          tell: $this.tell,
          lable: $this.lable,
          type: $this.type,
          address: $this.address,
          age: $this.age,
          daiyu: $this.daiyu,
          xueli: $this.xueli,
          uname: $this.uname,
          sameimg: $this.imgList.join(','),
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/addsamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '发布成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .img_service{
    width: 32.3%;
  }
  .div-bar2{
    margin: 40px 5px 0px 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  .div-bar3{
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .select-bar{
    width: 100px;
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    margin-left: 8%;
    border: 0;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
    width: 50%;
    padding: 5px;
  }
  .login_bar{
    padding: 12px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



